<script>
import MixinMapLine from '../../mixins/MapLine.js';
import { gmapApi } from 'vue2-google-maps';

export default {
  name: 'google-line',
  render: () => null,
  mixins: [MixinMapLine],

  data() {
    return {
      features: null,
      dataLayer: null,
    };
  },

  mounted() {
    this.initialize();
  },

  watch: {
    coordinates() {
      this.features.forEach((f) => this.dataLayer.remove(f));
      this.features = this.dataLayer.addGeoJson(this.geoJson);
    },

    color() {
      this.setStyles();
    },

    width() {
      this.setStyles();
    },
  },

  computed: {
    google: gmapApi,
  },

  methods: {
    initialize() {
      let mapPromise = this.$parent.$mapPromise
        ? this.$parent.$mapPromise
        : this.$parent.$parent.$mapPromise
        ? this.$parent.$parent.$mapPromise
        : null;
      if (!mapPromise) {
        return;
      }

      mapPromise.then((map) => {
        this.dataLayer = new this.google.maps.Data({ map: map });
        this.features = this.dataLayer.addGeoJson(this.geoJson);
        this.setStyles();
      });
    },

    setStyles() {
      this.dataLayer.setStyle(() => {
        return {
          strokeColor: this.color,
          strokeWeight: this.width,
        };
      });
    },
  },
};
</script>