export default {
    props: {
        coordinates: {
            required: true
        },

        color: {
            required: false,
            default: 'rgba(0,0,255, 0.2)'
        },

        width: {
            required: false,
            default: 5
        }
    },

    computed: {
        geoJson() {
            return {
                "type": "Feature",
                "geometry": {
                    "type": "LineString",
                    "coordinates": this.coordinates
                }
            }
        }
    }
}